<template>
  <figure>
    <header>
      <img class="img-fluid" :src="image" :alt="alt">
    </header>
    <figcaption>
      <div class="content">
        <slot></slot>
      </div>
    </figcaption>
  </figure>
</template>

<script>
  export default {
    name: 'FigureBox',

    props: {
      image: {
        type: String,
        default: 'https://placehold.it/600',
      },
      alt: {
        type: String,
        default: '',
      },
    },
  }
</script>

<style lang="scss" scoped>
  figure {
    background: white;
    border-radius: 4px;
    border: 0;
    box-shadow: rgba(0, 0, 0, 0.18) 0 1px 2px;
  }

  figure img {
    border-radius: 4px;
  }

  header {
    overflow: hidden;
    height: 280px;
  }

  figcaption .content {
    padding: 30px;
  }
</style>



